import React, { useEffect, useState, useReducer } from 'react'
import Banner from '../../components-reusable/Banner'

import {
  Card,
  Upload,
  Typography,
  Table,
  Image,
  Space,
  Button,
  Modal,
  Form,
  Input,
} from 'antd'
import {
  EditOutlined,
  EllipsisOutlined,
  SettingOutlined,
  UploadOutlined,
} from '@ant-design/icons'
// Similiar to Our History Page

import axios from 'axios'

import { general } from '../../helper-files/images'
const { churchWide } = general

const EditEnglish = () => {
  const { Title } = Typography

  const columns = [
    {
      title: 'title',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: 'congregation',
      dataIndex: 'congregation',
      key: 'congregation',
    },
    {
      title: 'description',
      dataIndex: 'description',
      key: 'description',
    },
    {
      title: 'image',
      key: 'image',
      render: (text, record) => (
        <>
          <Image
            width={200}
            src={record.image}
            fallback="data:image/png;base64,"
          />
        </>
      ),
    },
    {
      title: 'time',
      dataIndex: 'time',
      key: 'time',
    },
    {
      title: 'location',
      dataIndex: 'location',
      key: 'location',
    },

    {
      title: 'Action',
      key: 'action',
      render: (text, record) => (
        <Space size="middle">
          <button
            className='edit-delete-link'
            onClick={() => {
              setIsModalVisible(true)
              setRecordToEdit(record)
            }}
          >
            Edit/Delete
          </button>
        </Space>
      ),
    },
  ]
  const [data, setData] = useState()
  const [isModalVisible, setIsModalVisible] = useState(false)
  const [record, setRecordToEdit] = useState({})
  const [imageToUpload, setImageToUpload] = useState()
  const handleOk = () => {
    setIsModalVisible(false)
  }

  const handleCancel = () => {
    setIsModalVisible(false)
  }
  const onFinish = (values) => {
    console.log('Success:', values)
  }

  const onFinishFailed = (errorInfo) => {
    console.log('Failed:', errorInfo)
  }
  const handleChange = (event) => {
    Object.entries(record).forEach((i) => {
      record[event.target.name] = event.target.value
      setRecordToEdit(record)
    })
  }
  const onCreateImage = (event) => {
    const MAX_IMAGE_SIZE = 1000000

    let reader = new FileReader()
    reader.onload = (e) => {
      console.log(e.target)
      console.log('length: ', e.target.result.includes('data:image/jpeg'))
      if (!e.target.result.includes('data:image/jpeg')) {
        return alert('Wrong file type - JPG only.')
      }
      if (e.target.result.length > MAX_IMAGE_SIZE) {
        return alert('Image is loo large.')
      }

      setImageToUpload(e.target.result)

      record['image'] = e.target.result
      setRecordToEdit(record)
      console.log(record)
    }
    reader.readAsDataURL(event.target.files[0])
  }

  //API calls

  useEffect(() => {
    try {
      axios.get('http://localhost:8080/service').then((res) => {
        console.log(res.data)
        var resultArray = []
        res.data.forEach((i) => {
          if (i.congregation === 'English') {
            resultArray.push(i)
          }
        })
        setData(resultArray)
      })
    } catch (err) {
      console.log(err)
    }
  }, [])

  const addRecord = () => {
    axios.post('http://localhost:8080/service', {
      title: 'Fill in here',
      congregation: 'English',
      description: 'Fill in here',
      image: 'Fill in here',
      time: 'Fill in here',
      location: 'Fill in here',
    })
  }
  const saveRecord = () => {
    console.log(record.body)
    axios.put('http://localhost:8080/service', record)
  }
  const deleteRecord = () => {
    axios.delete('http://localhost:8080/service', { data: record })
  }

  return (
    <>
      <Banner imageTitlePairs={[{ title: 'Edit English Service Page', image: churchWide }]} />

      <div className="main-container">
        <br />
        <Title style={{ textAlign: 'center' }}>Edit English Service Page</Title>
        <hr className="thick-line"></hr>
        <br />
        <br />

        <Table columns={columns} dataSource={data} />
        <Button
          key="Add"
          onClick={() => {
            addRecord()
            window.location.reload()
          }}
        >
          Add Row
        </Button>
        <br />
        {/*Popup modal */}
        <Modal
          title="Basic Modal"
          visible={isModalVisible}
          onOk={handleOk}
          onCancel={handleCancel}
          footer={[
            <Button
              key="cancel"
              onClick={() => {
                handleCancel()
              }}
            >
              Cancel
            </Button>,
            <Button
              key="submit"
              type="primary"
              onClick={() => {
                saveRecord()

                setIsModalVisible(false)
                window.location.reload()
              }}
            >
              Save
            </Button>,
            <Button
              key="delete"
              type="primary"
              danger
              onClick={() => {
                deleteRecord(record)
                window.location.reload()
              }}
            >
              Delete
            </Button>,
          ]}
        >
          <Form
            name="basic"
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
          >
            <Form.Item
              label="title"
              name="title"
              onChange={(e) => handleChange(e)}
              rules={[
                {
                  required: true,
                  message: 'Please input the title',
                },
              ]}
            >
              {/*Need to change default*/}
              <Input
                name="title"
                defaultValue={record.title ? record.title : ''}
              />
            </Form.Item>
            <Form.Item
              label="Description"
              name="Description"
              onChange={(e) => handleChange(e)}
              rules={[
                {
                  required: true,
                  message: 'Please input the description!',
                },
              ]}
            >
              {/*Need to change default*/}
              <Input
                name="description"
                defaultValue={record.description ? record.description : ''}
              />
            </Form.Item>
            <Form.Item>
              <input
                type="file"
                name="image"
                onChange={(e) => {
                  onCreateImage(e)
                }}
              />
            </Form.Item>
            <Form.Item
              label="time"
              name="time"
              onChange={(e) => handleChange(e)}
              rules={[
                {
                  required: true,
                  message: 'Please input the Time!',
                },
              ]}
            >
              {/*Need to change default*/}
              <Input
                name="time"
                defaultValue={record.time ? record.time : ''}
              />
            </Form.Item>
            <Form.Item
              label="location"
              name="location"
              onChange={(e) => handleChange(e)}
              rules={[
                {
                  required: true,
                  message: 'Please input the Location!',
                },
              ]}
            >
              {/*Need to change default*/}
              <Input
                name="location"
                defaultValue={record.location ? record.location : ''}
              />
            </Form.Item>
          </Form>
        </Modal>
      </div>
    </>
  )
}

export default EditEnglish
